<template>
	<div class="back flex align_center border-bottom">
		<div class="flex align_center back_btn" @click="goBack" v-if="show">
			<md-icon name="arrow-left" size="lg"></md-icon>
		</div>
		<div v-else class="back_btn_space"></div>
		<div class="title">{{title}}</div>
	</div>
</template>

<script>
	export default {
		name: 'go_back',
		data() {
			return {
				
			}
		},
		props: {
			title: {
				type: String,
				default: ''
			},
			todo: {
				type: Boolean,
				default: false
			},
			show: {
				type: Boolean,
				default: true
			}
		},
		methods: {
			goBack() {
				if (this.todo) {
					this.$parent.todo();
				} else {
					this.$router.back();
				}
			}
		}
	}
</script>

<style scoped="scoped">
	.back {
		font-size: 3.8vw;
		background-color: #1acfc6;
		color: #fff;
		height: 14vw;
	}
	.back .back_btn {
		padding: 4vw;
		height: 6vw;
		color: #fff;
	}
	.back_btn_space {
		padding: 4vw 2vw;
	}
</style>
