import axios from 'axios'
const baseUrl = window.configs.api;
let storage = window.localStorage;
let HOME = '/home'
let LOGIN = '/login'
let loadingToast = {}
//错误返回
const statusVilter = function(data,_this) {
	if (data) {
		if (data.code == '0') {
			//
		} else {
			if (data.msg) {
				_this.$toast.failed(data.msg);
			} else {
				_this.$toast.failed('发生未知错误，请稍后再试');
				if (_this.$route.name != 'login') {
					setTimeout(()=>{
						_this.$router.push(LOGIN)
					},2000)
				}
			}
		}
	}
}
//请求失败再次请求
//在main.js设置全局的请求次数，请求的间隙
axios.defaults.retry = 4;
axios.defaults.retryDelay = 1000;

axios.interceptors.response.use(undefined, async function axiosRetryInterceptor(err) {
    var config = err.config;
    // If config does not exist or the retry option is not set, reject
    if(!config || !config.retry) return Promise.reject(err);
    // Set the variable for keeping track of the retry count
    config.__retryCount = config.__retryCount || 0;
    // Check if we've maxed out the total number of retries
    if(config.__retryCount >= config.retry) {
        // Reject with the error
        return Promise.reject(err);
    }
    // Increase the retry count
    config.__retryCount += 1;
    // Create new promise to handle exponential backoff
    var backoff = new Promise(function(resolve) {
        setTimeout(function() {
            resolve();
        }, config.retryDelay || 1);
    });
    // Return the promise in which recalls axios to retry the request
    await backoff;
	return axios(config);
});


export default {
    install (Vue) {
		Vue.prototype.$baseUrl = function () {
			return baseUrl
		}
		Vue.prototype.$school = function() {
			return window.configs.school
		}
		Vue.prototype.$system = function() {
			return window.configs.system
		}
		Vue.prototype.$title = function() {
			return window.configs.title
		}

		Vue.prototype.$get = function (urls, param, success, callback) {
			const _this = this
			let url = baseUrl + urls
			param = param || {}
			let headers = {
				'Content-Type':'application/json;charset=utf-8'
			}
			axios.get(url,{ params: param }, {
				headers: headers,
				withCredentials: false,
				timeout: 1000*30 //30秒超时限制
			}).then(function(res){
				const data = res.data;
				if (param.test && param.test == 'test') {
					if (success) success(data)
				} else { 
					if (data.code == '0') {
						if (success) success(data,res)
						if (callback) callback(data)
					} else {
						if (callback) {
							callback(data)
							return
						}
						statusVilter(data,_this)
					}
				}
			}).catch(function () {
				if (callback) callback()
				_this.$toast.failed('服务异常');
            })
		}



        Vue.prototype.$post = function (urls, param, success, callback) {
            const _this = this
            let url = baseUrl + urls
            param = param || {}
			let headers = {
				'Content-Type': 'application/x-www-form-urlencoded'
			}
            axios.post(url, param, {
                transformRequest: [function (data) {
                    let ret = ''
                    for (let it in data) { ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&' }
                    return ret
                }],
                headers: headers,
                withCredentials: false,
				timeout: 1000*30 //30秒超时限制
            }).then(function (res) {
                const data = res.data
				if (param.test && param.test == 'test') {
					if (success) success(data)
				} else {
					if (data.code == '0') {
						if (success) success(data,res)
						if (callback) {
							setTimeout(()=>{
								callback(data)
							},2000)
						}
					} else {
						if (callback) {
							callback(data)
							return
						}
						statusVilter(data,_this)
					}
				}
            }).catch(function () {
				if (callback) callback()
				_this.$toast.failed('服务异常');
            })
        }

        Vue.prototype.$postL = function (url, param, success, callback) {
			this.$toast.loading('loading')
            this.$post(url, param, success, (data)=>{
				if (callback) callback()
				statusVilter(data,this)
			})
        }

		Vue.prototype.$getL = function (url, param, success, callback) {
			this.$toast.loading('loading')
			this.$get(url, param, success, (data) => {
				if (callback) callback()
				statusVilter(data,this)
			})
		}


        //要求子数组名为：children
        Vue.prototype.$trav = function (arr, cFun, pFun, callback) {
            arr = arr || []
            cFun = cFun || function () {}
            pFun = pFun || function () {}

            for (let i = 0; i < arr.length; i++) {
                let children = arr[i].children
                pFun(arr[i])
                if(children.length){
                    //含子数组
                    for (let j = 0; j < children.length; j++) {
                        let item = children[j]
                        cFun(item)
                    }
                }
            }
            if(callback) callback()
        }
    },
    baseUrl,
    zcPost: function (url, param, success, callback) {
        url = baseUrl + url
        axios.post(url, param, {
            transformRequest: [function (data) {
                let ret = '';
                for (let it in data) { ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&' }
                return ret
            }],
            headers: {'Content-Type':'application/x-www-form-urlencoded'}
        }).then(function(res){
            const data = res.data
            if (data.status === '0') {
                if (success) success(data)
            } else if (data.status === '-1') window.console.log(data.msg)
            else if (data.status === '-2') window.console.log('登陆超时，请重新登录')
            else window.console.log('Unknown')
            if (callback) callback()
        }).catch(function(err){
            window.console.log(err)
            if(callback) callback()
        })
    }
}
