import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
	routes: [
		{
			path: '/',
			redirect: '/home'
		},
		{
			path: '/home',
			name: 'home',
			component: () => import('@/views/home'),
			meta: {
				title: '首页'
			}
		},
		{
			path: '/login',
			name: 'login',
			component: () => import('@/views/login'),
			meta: {
				title: '登录'
			}
		},
		{
			path: '/realtimecheckingsleep/index',
			name: 'realtimecheckingsleep_index',
			component: () => import('@/views/RealTimeCheckingSleep/index'),
			meta: {
				title: '实时查寝'
			}
		},
		{
			path: '/historyunback/index',
			name: 'historyunback_index',
			component: () => import('@/views/HistoryUnBack/index'),
			meta: {
				title: '历史未归'
			}
		},
		{
			path: '/report/index',
			name: 'report_index',
			component: () => import('@/views/Report/index'),
			meta: {
				title: '宿管综合'
			}
		},
		{
			path: '/report/list',
			name: 'report_list',
			component: () => import('@/views/Report/list'),
			meta: {
				title: '宿管综合'
			}
		},
		{
			path: '/report/stulist',
			name: 'report_stulist',
			component: () => import('@/views/Report/stulist'),
			meta: {
				title: '宿管综合'
			}
		},
		{
			path: '/report2/index',
			name: 'report2_index',
			component: () => import('@/views/Report2/index'),
			meta: {
				title: '学院综合'
			}
		},
		{
			path: '/report2/list',
			name: 'report2_list',
			component: () => import('@/views/Report2/list'),
			meta: {
				title: '学院综合'
			}
		},
		{
			path: '/search/index',
			name: 'search_index',
			component: () => import('@/views/Search/index'),
			meta: {
				title: '智能查询/修改'
			}
		},
		{
			path: '/search/list',
			name: 'search_list',
			component: () => import('@/views/Search/list'),
			meta: {
				title: '陌生人查询'
			}
		},
		{
			path: '/photo_upload/index',
			name: 'photo_upload',
			component: () => import('@/views/PhotoUpload/index'),
			meta: {
				title: '照片上传'
			}
		},
		{
			path: '/traffic_statistics/index',
			name: 'traffic_statistics',
			component: () => import('@/views/TrafficStatistics/index'),
			meta: {
				title: '通行统计'
			}
		},
		{
			path: '/traffic_statistics/list',
			name: 'traffic_list',
			component: () => import('@/views/TrafficStatistics/list'),
			meta: {
				title: '通行统计 - 通行记录'
			}
		},
		{
			path: '/add_ts/index',
			name: 'add_ts',
			component: () => import('@/views/AddTS/index'),
			meta: {
				title: '添加师生'
			}
		},
		{
			path: '/add/index',
			name: 'add',
			component: () => import('@/views/Add/index'),
			meta: {
				title: '添加访客'
			}
		},
		{
			path: '/add_yellow/index',
			name: 'add_yellow',
			component: () => import('@/views/AddYellow/index'),
			meta: {
				title: '添加黄名单'
			}
		},
		{
			path: '/push/index',
			name: 'push_index',
			component: () => import('@/views/Push/index'),
			meta: {
				title: '推送详情'
			}
		},
		{
			path: '/notice_detail',
			name: 'notice_detail',
			component: () => import('@/views/NoticeDetail/index'),
			meta: {
				title: '考勤结果详情'
			}
		},
		{
			path: '/notice_detail/list',
			name: 'notice_detail_list',
			component: () => import('@/views/NoticeDetail/list'),
			meta: {
				title: '考勤结果详情'
			}
		},
		{
			path: '/record/index',
			name: 'record',
			component: () => import('@/views/Record/index'),
			meta: {
				title: '通行记录'
			}
		},
		{
			path: '/news/index',
			name: 'news_index',
			component: () => import('@/views/News/index'),
			meta: {
				title: '消息'
			}
		},
		{
			path: '/news/detail',
			name: 'news_detail',
			component: () => import('@/views/News/detail'),
			meta: {
				title: '消息回复'
			}
		},
		{
			path: '/upload/index',
			name: 'upload_index',
			component: () => import('@/views/Upload/index'),
			meta: {
				title: '消息回复'
			}
		},
		{
			path: '/apply/index',
			name: 'apply_index',
			component: () => import('@/views/Apply/index'),
			meta: {
				title: '请假实习'
			}
		},
		{
			path: '/apply/list',
			name: 'apply_list',
			component: () => import('@/views/Apply/list'),
			meta: {
				title: '申请记录'
			}
		},
		{
			path: '*',
			component: () => import('@/views/404'),
			hidden: true
		},
		{
			path: '/demo',
			name: 'test_demo',
			component: () => import('@/test/demo'),
			meta: {
				title: 'demo测试'
			}
		},
	]
})


router.beforeEach((to, from, next) => {
	/* 路由发生变化修改页面title */
	if (to.meta.title) {
		document.title = window.configs.headertitle + to.meta.title
	}
	let user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : {}
	if (user.user_id) {
		next();
	} else {
		if (to.path == '/login') {
			next();
		} else {
			next({path: "/login"});
		}
	}
})

export default router
